import uniIcons from '@/components/uni-icon/uni-icon.vue'
export default {
	components: {
		uniIcons
	},
	props:{
		text: {
			type: String,
			default: '暂无未读消息'
		}, 
		color: {
			type: String,
			default: '#de8c17'
		},
		bgColor: {
			type: String,
			default: '#fffbe8'
		},
		switchTime: {
			type: Number,
			default: 3
		},
		showSerial: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			number: 0,
			list: [],
			copyText: '',
			show: '',
		};
	},
	mounted() {
		this.list = this.text.split('|');
		this.show = true;
		/* this.number = 0;
		this.startMove(); */
	},
	watch: {
		text: function () {
			this.show = true;
			if(this.text != this.copyText){
				this.copyText = this.text;
				this.list = this.text.split('|');
			}
			/* this.number = 0;
			this.startMove(); */
		}
	},
	methods: {			
		/* startMove () {
		  // eslint-disable-next-line
		  let timer = setTimeout(() => {
			if (this.number === this.list.length-1) {
			  this.number = 0;
			} else {
			  this.number += 1;
			}
			this.startMove();
		  }, this.switchTime*1000);
		}, */
		more(){
			this.show = false;
			this.$emit('more')
		},
		handleClick(index, text) {
			this.$emit('click', index, text)
		},
		handleChange(res) {
			this.$emit('change', res)
		}
	}
}
